.container{
    min-height: 100%;
}
.mensagens{
    width: 100%;
    margin-bottom: 50vh;
}

.mensagens p{
    
    font-size: 46px;
    color: #957DAD;
    font-weight: bold;
    text-align: center;
    margin-bottom: 25vh;
    width: 100%;
}

button{
    margin-top: 2vh;
    border: 0;
    border-radius: 3px;
    width: 100%;
    font-size: 26px;
    padding: 25px;
    color: #E9E2D7;
    background: #704523;
}

@media only screen and (max-width:360px){
    .mensagens p{
        margin: 15vh auto ;
        font-size: 24px;
        
    }

    button{
        padding: 12px;
        font-size: 16px;
        
    }
}